
var kaObj = JSON.parse(sessionStorage.kaObj)
var userName = localStorage.userName
var passWord = localStorage.passWord

import Search from 'vux/src/components/search/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import { mapMutations,mapState } from 'vuex'
import pannel from './children/pannel'
import close from './children/close'
export default {
name:"add",
components:{
Search,
XButton,
Divider,
pannel,
close,
Checklist
},
data(){
return{
value:"",      //
valueA:[],     //
results:[],    //
isShow:false,  //div
docmHeight: document.documentElement.clientHeight,  //
showHeight: document.documentElement.clientHeight,
hidshow:true ,  //
}
},
methods:{
...mapMutations(['setChooseList','scanChooseList','checkedArr','cancelCheckedArr']),       //mutationstate
getResult(val){  //
this.results = val? getResult(this.value):[]
},
choose(val){   //
this.checkedArr(val)
},
resultClick(item){  //
this.isShow = false
this.value = ''
this.$axios.get(this.url+'preorderKaController.do?checkProducts',{
params:{
userName:userName,
passWord:passWord,
customerCode:kaObj.customerCode,
storescode:kaObj.storescode,





}
}).then(res=>{
console.log(res)
if(res.data.success == true){
this.setChooseList(item)           
if(this.isHave){
let str = this.isHaveArr[0].name
this.$vux.alert.show({
title: '',
content: str+',',
})
}else{
this.$vux.toast.show({
time:'1000',
type:'success',
text: ''
})
}

}else{
this.$vux.alert.show({
title: '',
content: res.data.msg,
})
}
}).catch(e=>{
this.$vux.alert.show({
title: '',
content: ',',
})
})
},
cancel(){  //
this.$store.commit('changeHeaderRight','')
this.isShow = false
this.results = []
this.value = ''
this.valueA = []
this.cancelCheckedArr()
},
onFocus(){ //
this.$refs.titS.style.zIndex ='999'
this.results = []
this.isShow = true
if(this.value){
this.value = ''
this.results = []
}
},
onBlur(){
this.isShow = true
},
open(){
let _this = this
_this.$wechat.scanQRCode({
needResult: 1, // 01
scanType: ["barCode"], // 
success: function (res) {



let code = res.resultStr

let codeT = code.split(',')[1]
if(codeT){

let productListObj = JSON.parse(sessionStorage.productList)
let sapIds =[]
let opt = ''
for(let j in productListObj){
if(productListObj[j].barcode == codeT){
sapIds.push(productListObj[j].productSapId)

}
if(j == productListObj.length-1 ){

for(let j in sapIds){
if(opt!=''){
opt=opt+','+sapIds[j]
}else{
opt+=sapIds[j]
}

}

}

}

_this.$axios.get(_this.url+'preorderKaController.do?checkProducts',{
params:{
userName:userName,
passWord:passWord,
customerCode:kaObj.customerCode,
storescode:kaObj.storescode,
sapIds:opt
}
}).then(res=>{
console.log(res)
if(res.data.success == true){
_this.$refs.titS.style.zIndex ='999'
let item = []
for(let i in res.data.products){
let obj ={
title:res.data.products[i].itemdesc,
id:res.data.products[i].productSapId,
spec:res.data.products[i].itemspec,
num:0
}
item.push(obj)
}
if(item.length){
_this.scanChooseList(item)           
}
if(_this.isHave){
let strC=''
if(_this.isHaveArr.length == 1){
strC+=_this.isHaveArr[0].name
}else{
for(let j in _this.isHaveArr){  //
strC = _this.isHaveArr[j]+'</br>'+strC
}
}
_this.$vux.alert.show({
title: ',',
content: strC,
})
}else{
_this.$vux.toast.show({
time:'1000',
type:'success',
text: ''
})
}

}else{
_this.$vux.alert.show({
title: '',
content: res.data.msg,
})
}
}).catch(e=>{
_this.$vux.alert.show({
title: '',
content: ',',
})
})

}else{
_this.$vux.toast.show({
time:'1000',
type:'cancel',
text: ''
})
}
}
});
},

},
computed:{   //
...mapState(
['index','isHave','pannelList','getChooseList','isShowDiv','isHaveArr','changeHeaderRight','isClick']
),
},
created(){
let url = location.href.split('#')[0]    
this.$axios.get('http://171.8.66.195:8080/synear/orderReceiptController.do?getWechatConfigureList'+'&url='+url  ,{
}).then(res =>{
console.log(res)
let data = JSON.parse(res.data.slice(1,res.data.length-1))
console.log(data)
this.$wechat.config({
debug: false, // ,apialertpclogpc
appId: 'wxc61954ab5a7b5efb', // 
timestamp: data.timestamp, // 
nonceStr: data.noncestr, // 
signature: data.signature, // 
jsApiList: ['scanQRCode'] 
})
})
this.$wechat.error(function (res) {
console.log(res)
alert("" + res.errMsg);
});
},
watch:{

showHeight:function() {
if(this.docmHeight > this.showHeight){
this.hidshow=false
this.bus.$emit('change',this.hidshow)   
}else{
this.hidshow=true
this.bus.$emit('change',this.hidshow)
}
},
results(val){   //,
if(val.length){
this.$store.commit('changeHeaderRight','')
}

},
isClick(){    //,
if(this.isClick){
this.results = []

this.value = ''
this.valueA = []

this.$store.commit('isClick',false)
}
}
},
mounted(){

window.onresize = ()=>{
return(()=>{
this.showHeight = document.body.clientHeight;
})()
}

this.$axios.get(this.url+'preorderKaController.do?getProductList',{
params:{
userName:userName,
passWord:passWord,
customerid:kaObj.customerid



}
}).then(res=>{
console.log(res)
if(res.data.rows.length){
sessionStorage.productList = JSON.stringify(res.data.rows)
this.$store.commit('getChooseList')
}
}).catch(e=>{
this.$vux.alert.show({
title: '',
content: ',',
})
})
},
}
function getResult(val){
let productList = JSON.parse(sessionStorage.productList)
let re = []

productList.map(function(item){
if(item.itemdesc.search(val) != -1){
re.push({




value:item.itemdesc,    //
key:item.productSapId,  //id
})
}
})

return re
}



